.crewCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    width: 10rem;
    height: 17rem;
    padding: 0.7rem 0.2rem 0.3rem;
    border-radius: 1rem;
}

.crewCard_dm {
    background-color: #404040;
    color: white;
}

.crewCard_lm {
    background-color: white;
}

.crewImage {
    width: 88%;
    border-radius: 1rem;
}

.character {
    font-size: 0.9rem;
    font-weight: 400;
}

.castName {
    font-size: 0.8rem;
    font-weight: 200;
}