.background {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    width: calc(100vw - (100vw - 100%));
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;
}

.background_dm {
    background: rgba(0, 0, 0, 0.64);
    backdrop-filter: blur(13.2px);
}

.background_lm {
    background: rgba(196, 190, 190, 0.78);
    backdrop-filter: blur(13.2px);
}

.infoBox {
    width: 100rem;
    height: 50rem;
    border-radius: 1rem;
    z-index: 4;

    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.infoBox_dm {
    background-color: #212224;
}

.infoBox_lm {
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    background-color: rgb(246, 246, 246);
}

.infoBoxPortraitOuter {
    display: none;
}

.infoBoxImgPortrait {
    width: 30%;
    border-radius: 1rem;
    z-index: 5;
}

.infoBoxData {
    width: 60%;
    height: 45rem;
    z-index: 5;

    display: grid;
    grid-template-rows: 6rem 5rem 1fr 19rem 3rem;
    grid-template-columns: 100%;
    align-items: center;
    justify-items: center;
}

.infoBoxData_dm {
    color: white;
}

.infoBoxData_lm {
    color: black;
}

.head {
    text-align: center;
}

.title {
    font-size: 3rem;
    font-weight: 500;
}

.tagline {
    font-size: 1.2rem;
    font-weight: 400;
}

.extraInfo {
    width: 100%;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.info {
    width: 13rem;
    height: 3rem;
    border-radius: 5rem;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.info_dm {
    background-color: #404040;
}

.info_lm {
    background-color: white;
}

.infoHeading {
    text-transform: capitalize;
    font-size: 1.2rem;
    font-weight: 400;
}

.infoData {
    font-size: 1rem;
    font-weight: 200;
}

.overviewBox {
    height: 100%;
    width: 100%;
    padding: 1rem 2rem;
    border-radius: 1.5rem;

    overflow: scroll;
}

.overviewBox_dm {
    background-color: #404040;
}

.overviewBox_lm {
    background-color: white;
}

.overviewBox::-webkit-scrollbar {
    width: 0 !important
}

.overviewHeading {
    font-size: 1.7rem;
    font-weight: 500;
    text-align: center;
    text-transform: capitalize;
    margin-bottom: 0.7rem;
}

.overview {
    font-size: 1.1rem;
    font-weight: 200;
}

.carousel {
    width: 100%;
    height: 17rem;
    /* display: flex;
    justify-content: center;
    align-items: center; */
}

.trailerButton {
    width: 18rem;
    height: 2.5rem;
    border-radius: 2rem;

    display: grid;
    justify-content: center;
    align-items: center;

    text-transform: capitalize;

    color: white;
    background-color: #e50914;
}

.trailerButton:hover {
    background-color: #bd0812;
}

@media (max-width: 1750px) {
    .infoBox {
        width: 90rem;
    }
}

@media (max-width: 1600px) {
    .infoBox {
        width: 80rem;
    }
}

@media (max-width: 1400px) {
    .infoBox {
        width: 70rem;
    }
}

@media (max-width: 1250px) {
    .infoBox {
        width: 60rem;
    }

    .infoBoxImgPortrait {
        width: 40%;
    }

    .infoBoxData {
        width: 55%;
    }

    .title {
        font-size: 2.5rem;
    }

    .tagline {
        font-size: 1.2rem;
    }

    .info {
        width: 10rem;
        height: 3rem;
    }

    .infoHeading {
        font-size: 1.1rem;
    }

    .infoData {
        font-size: 0.9rem;
    }
}

@media (max-width: 1100px) {
    .infoBox {
        display: none;
    }

    .infoBoxPortraitOuter {
        display: block;
        width: 50rem;
        height: 50rem;
        padding: 2rem;
        border-radius: 1rem;
        z-index: 4;
    }

    .infoBoxPortraitOuter_dm {
        background-color: #212224;
    }

    .infoBoxPortraitOuter_lm {
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        background-color: rgb(246, 246, 246);
    }

    .infoBoxPortrait {
        height: 100%;

        overflow-y: scroll;
        overflow: -moz-scrollbars-none;
        -ms-overflow-style: none;
    }

    .infoBoxPortrait::-webkit-scrollbar {
        width: 0 !important
    }

    .infoBoxImgLandscape {
        width: 100%;
        border-radius: 1rem;
        z-index: 5;
    }

    .infoBoxData {
        width: 100%;
        height: 55rem;
        grid-template-rows: 6rem 6rem 1fr 20rem 5rem;
    }
}

@media (max-width: 900px) {
    .infoBoxPortraitOuter {
        width: 40rem;
    }
}

@media (max-width: 700px) {
    .infoBoxPortraitOuter {
        width: 30rem;
    }

    .title {
        font-size: 2rem;
    }

    .info {
        width: 8rem;
        height: 2.7rem;
    }

    .infoHeading {
        font-size: 0.95rem;
    }

    .infoData {
        font-size: 0.8rem;
    }
}

@media (max-width: 550px) {
    .infoBoxPortraitOuter {
        width: 27rem;
    }

    .info {
        width: 7.5rem;
        height: 2.5rem;
    }
}

@media (max-width: 480px) {
    .background {
        padding: 0.2rem;
    }

    .infoBoxPortraitOuter {
        width: calc(100vw - (100vw - 100%));
    }

    .info {
        width: 7rem;
    }

    .infoHeading {
        font-size: 0.9rem;
    }

    .infoData {
        font-size: 0.7rem;
    }

    .overviewHeading {
        font-size: 1.5rem;
    }
}

@media (max-width: 445px) {
    .title {
        font-size: 1.5rem;
    }

    .info {
        width: auto;
        height: 2rem;
        padding: 1rem 0.9rem;
    }

    .infoHeading {
        font-size: 0.83rem;
    }

    .infoData {
        font-size: 0.75rem;
    }
}

@media (max-height: 50rem) {
    .background {
        padding: 0.2rem;
    }

    .infoBox,
    .infoBoxPortraitOuter {
        height: calc(100vh - (100vh - 100%));
    }
}